import React from "react";
import { Text, TouchableOpacity, View, Image } from "react-native";
import { GlobalStyles } from "./GlobalStyles";
import { Ionicons } from "@expo/vector-icons";
import { MaterialCommunityIcons } from "@expo/vector-icons";
import { FontAwesome5 } from "@expo/vector-icons";
import IconImages from "../../assets/iconImages";

const SelectedFilterButton = ({
  selectedFilters,
  searchQuery,
  iconName,
  icon,
  checked,
  sdgId,
}) => {
  return sdgId ? (
    <TouchableOpacity style={GlobalStyles.sdgResults}>
      <View>
        <Image
          key={selectedFilters.selectedFilter[0]}
          source={IconImages[sdgId - 1].image}
          style={{ width: 91, height: 91 }}
          resizeMode={"cover"}
        />
      </View>
    </TouchableOpacity>
  ) : (
    <TouchableOpacity>
      {searchQuery ? (
        <View style={GlobalStyles.mainButton}>
          <Text style={GlobalStyles.normalText}>{searchQuery}</Text>
        </View>
      ) : (
        selectedFilters?.selectedFilter?.map((filter) => (
          <View style={GlobalStyles.mainButton} key={filter}>
            <Ionicons
              name="checkmark-sharp"
              size={20}
              color="black"
              style={{ marginRight: 4 }}
            />
            {checked && (
              <Ionicons
                name="checkmark-sharp"
                size={20}
                color="rgba(69, 55, 72, 1)"
                style={{ marginRight: 4 }}
              />
            )}
            {icon && (
              <FontAwesome5
                name={icon}
                size={20}
                color="rgba(69, 55, 72, 1)"
                style={{ paddingRight: 10 }}
              />
            )}
            {iconName && (
              <MaterialCommunityIcons
                name={iconName}
                size={24}
                color="rgba(69, 55, 72, 1)"
                style={{ paddingRight: 10 }}
              />
            )}
            <Text style={GlobalStyles.normalText}>{filter}</Text>
          </View>
        ))
      )}
    </TouchableOpacity>
  );
};

export default SelectedFilterButton;
