import React from "react";
import {
  Text,
  View,
  ScrollView,
  TouchableOpacity,
  ImageBackground,
} from "react-native";
import { GlobalStyles } from "../../components/globals/GlobalStyles";
import { LocalStyles } from "../../components/locals/LocalStyles";
import MainButton from "../../components/globals/MainButton";
import IconImages from "../../assets/iconImages";
import SearchBar from "./SearchBar";
import HomeFooter from "../../components/globals/HomeFooter";
import Sdgs from "./Sdgs";
import { LinearGradient } from "expo-linear-gradient";
import { useTranslation } from "react-i18next";
import newData from "../../data/organisations.json";
import Filters from "../../components/globals/Filters";

function Home({ navigation }) {
  const needs = [];
  newData.map((data) => needs.push(data.Needs.split(", ")));
  const needsFlat = needs.flat(1);
  const finalNeeds = needsFlat.filter(
    (item, index) => needsFlat.indexOf(item) === index
  );
  const { t } = useTranslation();
  return (
    <ScrollView>
      <View style={GlobalStyles.container}>
        <ImageBackground
          resizeMode="contain"
          source={require("../../assets/sdgsImage.png")}
          style={{
            justifyContent: "center",
            alignItems: "center",
          }}
          imageStyle={{
            paddingLeft: 600,
          }}
        >
          <View style={GlobalStyles.homepageContainer}>
            {/*<Image
              style={GlobalStyles.handImage}
              source={require("../../assets/Vector.png")}
        />*/}
            <Text style={GlobalStyles.bannerText}>{t("home.title")}</Text>
          </View>
        </ImageBackground>
        <View >
          <Text style={[GlobalStyles.normalText, GlobalStyles.ptpb]}>
            What do you need help with?
          </Text>
        </View>
        <View style={{ paddingBottom: 70 }}>
          <SearchBar />
        </View>
      </View>
      <View>
        <LinearGradient
          colors={[
            "rgba(255, 255, 255, 1)",
            "rgba(255, 249, 243, 0.63)",
            "rgba(248, 208, 171, 0.5)",
          ]}
          style={GlobalStyles.background}
        />
        <View style={GlobalStyles.whiteContainer}>
          <View style={GlobalStyles.homeButtonsGrid}>
            <MainButton answer={finalNeeds[0]} iconName="food-variant" />
            <MainButton answer={finalNeeds[1]} iconName="home-heart" />
            <MainButton
              answer={finalNeeds[4]}
              iconName="book-open-page-variant"
            />
            <MainButton answer={finalNeeds[3]} iconName="medical-bag" />
            <MainButton answer={finalNeeds[6]} iconName="account-hard-hat" />
            <MainButton answer={finalNeeds[2]} icon="coins" />
            <MainButton answer={finalNeeds[5]} iconName="human" />
            <MainButton answer={finalNeeds[7]} icon="comment-medical" />
          </View>
        </View>
      </View>
      <View style={{ backgroundColor: "white" }}>
        <View style={LocalStyles.allOrgsContainer}>
          <Text style={LocalStyles.allOrgsText}>All organizations</Text>
          <TouchableOpacity
            style={LocalStyles.accordionButton}
            onPress={() => {
              navigation.navigate("AccordionView");
            }}
          >
            <Text style={LocalStyles.accordionText}>
              {`See all filters \u2192`}
            </Text>
          </TouchableOpacity>
        </View>
        <Filters
          selectedFilters={{
            fieldToSearch: [],
            selectedFilter: ["threeOrgsPreview"],
          }}
        />
        <TouchableOpacity
          style={{ marginBottom: 75, marginTop: -20 }}
          onPress={() => {
            navigation.navigate("Results", {
              selectedFilters: {
                fieldToSearch: [],
                selectedFilter: ["All organizations"],
              },
            });
          }}
        >
          <Text style={GlobalStyles.textLink}>See all</Text>
        </TouchableOpacity>
      </View>
      <View style={GlobalStyles.sdgContainer}>
        <Text style={GlobalStyles.sdgTitle}>THE 17 GOALS</Text>
        <Text style={GlobalStyles.sdgText}>
          The NGOs we're partnering with contribute to the Sustainable
          Development Goals
        </Text>
        <View>
          <View style={GlobalStyles.sdgGrid}>
            {IconImages.map((icon, i) => (
              <Sdgs
                source={icon.image}
                answer={icon.name}
                sdgId={i + 1}
                key={i + 1}
              />
            ))}
          </View>
        </View>
      </View>
      <View style={GlobalStyles.homeFooter}>
        <HomeFooter />
      </View>
    </ScrollView>
  );
}

export default Home;
