import React from "react";
import { Text, View } from "react-native";
import { GlobalStyles } from "../components/globals/GlobalStyles";
import SelectedFilterButton from "../components/globals/SelectedFilterButton";
import { LocalStyles } from "../components/locals/LocalStyles";
import { ScrollView, TouchableOpacity } from "react-native-gesture-handler";
import { useNavigation } from "@react-navigation/native";
import { MaterialIcons } from "@expo/vector-icons";
import Filters from "../components/globals/Filters";
import Footer from "../components/globals/Footer";

export default function Results({ route }) {
  const navigation = useNavigation();
  const { selectedFilters, searchQuery, iconName, icon, checked, sdgId } =
    route.params;

  return (
    <ScrollView>
      <View style={GlobalStyles.roseContainer}>
        <View style={GlobalStyles.whiteContainer}>
          <View style={GlobalStyles.topFilterSection}>
            {(!selectedFilters && (
              <View>
                <Text style={GlobalStyles.searchResText}>
                  You searched for:
                </Text>
              </View>
            )) ||
              (!sdgId && (
                <View>
                  <Text style={GlobalStyles.searchResText}>
                    You searched for:
                  </Text>
                </View>
              ))}
          </View>
          <View style={LocalStyles.flexDirectionColumn}>
            <SelectedFilterButton
              selectedFilters={selectedFilters}
              searchQuery={searchQuery}
              iconName={iconName}
              icon={icon}
              checked={checked}
              sdgId={sdgId}
            />
          </View>
          <TouchableOpacity
            style={GlobalStyles.seeAllSection}
            onPress={() => navigation.navigate("AccordionView")}
          >
            <View style={LocalStyles.filterButton}>
              <Text style={GlobalStyles.normalText}>See all filters</Text>
            </View>
            <MaterialIcons name="arrow-right-alt" size={24} color="black" />
          </TouchableOpacity>
        </View>
      </View>
      <Filters selectedFilters={selectedFilters} searchQuery={searchQuery} />
      <Footer />
    </ScrollView>
  );
}
