import React from "react";
import { View, Text } from "react-native";
import { GlobalStyles } from "../components/globals/GlobalStyles";
import OrgCard from "./OrgCard";
import { LocalStyles } from "../components/locals/LocalStyles";
import { ScrollView } from "react-native-gesture-handler";

const OrganizationsList = ({ filteredOrganizations }) => {
  return (
    <ScrollView>
      <View style={GlobalStyles.cardsContainerLeft}>
        {filteredOrganizations?.length === 0 ? (
          <View>
            <Text style={LocalStyles.questionText}>
              No organizations found. Please select a different filter.
            </Text>
          </View>
        ) : (
          filteredOrganizations
            ?.filter(
              (item, index) => filteredOrganizations.indexOf(item) === index
            )
            .map((org) => (
              <OrgCard item={org} key={org["Name of Organisation"]} />
            ))
        )}
      </View>
    </ScrollView>
  );
};

export default OrganizationsList;
