import React from "react";
import { ScrollView, View, Text, Image, Linking } from "react-native";
import { FontAwesome } from "@expo/vector-icons";
import { Entypo } from "@expo/vector-icons";
import { SimpleLineIcons } from "@expo/vector-icons";
import { LocalStyles } from "../components/locals/LocalStyles";
import Footer from "../components/globals/Footer";
import { GlobalStyles } from "../components/globals/GlobalStyles";
import { LinearGradient } from "expo-linear-gradient";
import { makeCall } from "../utils/makeCall";

export default function OrganizationDetails({ route }) {
  const item = route.params;

  return (
    <ScrollView>
      <View style={LocalStyles.soloImageContainer}>
        <View style={LocalStyles.imageContainer}>
          <Image
            source={require("../assets/logos/no_logo.png")}
            style={LocalStyles.soloImageContainer}
            resizeMode="contain"
          />
        </View>
      </View>
      <View style={GlobalStyles.soloWhite}>
        <Text style={LocalStyles.name}>{item["Name of Organisation"]}</Text>
        {!item["Phone Number"] ? (
          <></>
        ) : (
          <View>
            <View style={LocalStyles.field}>
              <Entypo
                name="old-mobile"
                size={24}
                color="#6B6B6B"
                style={{ marginRight: 2, marginLeft: -4 }}
              />
              <View>
                <Text style={LocalStyles.fieldText}>Phone</Text>
              </View>
            </View>
            {item["Phone Number"]
              .split("/")
              .map((num) => num.trim())
              .map((num) => (
                <Text
                  key={"phone" + num}
                  style={LocalStyles.questionText}
                  onPress={() => makeCall(num)}
                >
                  {` ${num}`}
                </Text>
              ))}
          </View>
        )}
        <View>
          <View style={LocalStyles.field}>
            <FontAwesome
              name="building"
              size={20}
              color="#6B6B6B"
              style={{ marginRight: 4 }}
            />
            <View>
              <Text style={LocalStyles.fieldText}>Address</Text>
            </View>
          </View>
          {!item["Address"] ? (
            <></>
          ) : (
            <Text style={LocalStyles.questionText}>{item["Address"]}</Text>
          )}
          <Text style={LocalStyles.questionText}>{item["Code_region"]}</Text>
        </View>
        <View>
          {!item["Email"] ? (
            <></>
          ) : (
            <View>
              <View style={LocalStyles.field}>
                <FontAwesome
                  name="envelope"
                  size={20}
                  color="#6B6B6B"
                  style={{ marginRight: 6 }}
                />
                <View>
                  <Text style={LocalStyles.fieldText}>Email</Text>
                </View>
              </View>
              <View>
                <Text style={LocalStyles.questionText}>{item["Email"]}</Text>
              </View>
            </View>
          )}
        </View>
        <View>
          {!item["Web Address"] ? (
            <></>
          ) : (
            <View>
              <View style={LocalStyles.field}>
                <SimpleLineIcons
                  name="globe"
                  size={20}
                  color="#6B6B6B"
                  style={{ marginRight: 6 }}
                />
                <View>
                  <Text style={LocalStyles.fieldText}>Website</Text>
                </View>
              </View>
              <Text
                style={LocalStyles.websiteLink}
                onPress={() =>
                  Linking.openURL(item["Web Address"]).catch((err) =>
                    console.error("Couldn't load page", err)
                  )
                }
              >
                {item?.["Web Address"]}
              </Text>
            </View>
          )}
        </View>
      </View>
      <View style={{ backgroundColor: "rgba(255, 255, 255, 1)" }}>
        <LinearGradient
          colors={["rgb(239,236,232)", "rgb(255,255,255)"]}
          style={{ padding: 20 }}
        >
          <View style={LocalStyles.helpContainer}>
            <Text style={LocalStyles.name}>Areas of support</Text>
            <Text style={LocalStyles.questionText}>
              - {item["Needs"].split(", ").join("\n- ")}
            </Text>
          </View>
          <View style={LocalStyles.helpContainer}>
            <Text style={LocalStyles.name}>Target group</Text>
            <Text style={LocalStyles.questionText}>
              - {item["Target group"].split("\n").join("\n- ")}
            </Text>
          </View>
        </LinearGradient>
        <View style={GlobalStyles.soloWhite}>
          <Text style={LocalStyles.name}>Objective</Text>
          <Text style={LocalStyles.questionText}>{item["Objective"]}</Text>
        </View>
      </View>
      <Footer />
    </ScrollView>
  );
}
