import React from "react";
import {
  Image,
  Text,
  View,
  TouchableOpacity,
  ScrollView,
  Linking,
} from "react-native";
import { LocalStyles } from "../components/locals/LocalStyles";
import { FontAwesome } from "@expo/vector-icons";
import { Entypo } from "@expo/vector-icons";
import { useNavigation } from "@react-navigation/native";
import { SimpleLineIcons } from "@expo/vector-icons";
import { GlobalStyles } from "../components/globals/GlobalStyles";

const OrgCard = ({ item }) => {
  const navigation = useNavigation();

  const navigateToOrganizationDetails = () => {
    navigation.navigate("Organization", item);
  };

  return (
    <ScrollView style={LocalStyles.orgContainer}>
      <TouchableOpacity
        style={LocalStyles.cardStyle}
        onPress={navigateToOrganizationDetails}
      >
        <View style={LocalStyles.imageContainer}>
          <Image
            source={require("../assets/logos/no_logo.png")}
            style={LocalStyles.soloImageContainer}
            resizeMode="contain"
          />
        </View>
        <View style={GlobalStyles.soloWhite}>
          <Text style={LocalStyles.name}>{item["Name of Organisation"]}</Text>
          {!item["Phone Number"] ? (
            <></>
          ) : (
            <View>
              <View style={LocalStyles.field}>
                <Entypo
                  name="old-mobile"
                  size={24}
                  color="#6B6B6B"
                  style={{ marginRight: 2, marginLeft: -4 }}
                />
                <View>
                  <Text style={LocalStyles.fieldText}>Phone</Text>
                </View>
              </View>
              {item["Phone Number"]
                .split("/")
                .map((num) => num.trim())
                .map((num) => (
                  <Text key={"phone" + num} style={LocalStyles.questionText}>
                    {` ${num}`}
                  </Text>
                ))}
            </View>
          )}
          <View>
            <View style={LocalStyles.field}>
              <FontAwesome
                name="building"
                size={20}
                color="#6B6B6B"
                style={{ marginRight: 4 }}
              />
              <View>
                <Text style={LocalStyles.fieldText}>Address</Text>
              </View>
            </View>
            {!item["Address"] ? (
              <></>
            ) : (
              <Text style={LocalStyles.questionText}>{item["Address"]}</Text>
            )}
            <Text style={LocalStyles.questionText}>{item["Code_region"]}</Text>
          </View>
          <View>
            {!item["Web Address"] ? (
              <></>
            ) : (
              <View>
                <View style={LocalStyles.field}>
                  <SimpleLineIcons
                    name="globe"
                    size={20}
                    color="#6B6B6B"
                    style={{ marginRight: 6 }}
                  />
                  <View>
                    <Text style={LocalStyles.fieldText}>Website</Text>
                  </View>
                </View>
                <Text
                  style={LocalStyles.websiteLink}
                  onPress={() =>
                    Linking.openURL(item["Web Address"]).catch((err) =>
                      console.error("Couldn't load page", err)
                    )
                  }
                >
                  {item?.["Web Address"]}
                </Text>
              </View>
            )}
          </View>
        </View>
      </TouchableOpacity>
    </ScrollView>
  );
};

export default OrgCard;
