import React, { useEffect, useState } from "react";
import newData from "../../data/organisations.json";
import OrganizationsList from "../../screens/OrganizatonsList";
import { ScrollView } from "react-native-gesture-handler";
var levenshtein = require("fast-levenshtein");

const Filters = ({ selectedFilters, searchQuery }) => {
  const listOfOrgs = newData;
  // Make list random
  for (let i = listOfOrgs.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    let newDataElement = listOfOrgs[i];
    listOfOrgs[i] = newData[j];
    listOfOrgs[j] = newDataElement;
  }

  const [filteredOrganizations, setFilteredOrganizations] =
    useState(listOfOrgs);

  // For when gender is "Other", search as if it was "Men" (excludes NGOs that only help "Women")
  if (selectedFilters.selectedFilter?.includes("Other")) {
    for (let i = 0; i < selectedFilters.selectedFilter.length; i++)
      if (selectedFilters.selectedFilter[i] === "Other")
        selectedFilters.selectedFilter[i] = "Men";
  }

  useEffect(() => {
    if (!selectedFilters) {
      return;
    }
    if (selectedFilters.selectedFilter[0] !== "All organizations") {
      if (selectedFilters.selectedFilter[0] === "threeOrgsPreview") {
        setFilteredOrganizations(filteredOrganizations.slice(0, 3));
      } else if (searchQuery) {
        const searchQueryWords = searchQuery.split(" ");

        const findWords = (orgWords) => {
          return searchQueryWords.every((searchQueryWord) => {
            return searchQueryWord.length <= 3
              ? orgWords.some(
                  (word) => word.toLowerCase() == searchQueryWord.toLowerCase()
                )
              : searchQueryWord.length <= 5
              ? orgWords.some(
                  (word) =>
                    levenshtein.get(
                      word.toLowerCase(),
                      searchQueryWord.toLowerCase()
                    ) <= 1
                )
              : orgWords.some(
                  (word) =>
                    levenshtein.get(
                      word.toLowerCase(),
                      searchQueryWord.toLowerCase()
                    ) <= 2
                );
          });
        };

        setFilteredOrganizations(
          filteredOrganizations.filter((org) => {
            const allWordsOfThisOrg = [
              org["Name of Organisation"],
              org.Objective,
              org.Services,
              org.SDGs,
              org["Focus 1"],
              org["Focus 2"],
              org["Focus 3"],
              org["Focus 4"],
              org["Focus 5"],
              org["Focus 6"],
              org["Focus 7"],
              org["Focus 8"],
              org["Focus 9"],
              org["Focus 10"],
              org["Target group"],
              org.Needs,
              org.Age,
              org.Gender,
              org.Country,
              org.Region,
            ].flatMap((str) => str.split(" "));

            return findWords(allWordsOfThisOrg);
          })
        );
      } else {
        setFilteredOrganizations(
          filteredOrganizations.filter((org) => {
            return selectedFilters.selectedFilter.every((filterWord, i) =>
              org[selectedFilters.fieldToSearch[i]]?.includes(filterWord)
            );
          })
        );
      }
    }
  }, []);

  return (
    <ScrollView>
      <OrganizationsList filteredOrganizations={filteredOrganizations} />
    </ScrollView>
  );
};

export default Filters;
