import React, { useState } from "react";
import { ScrollView, Text, View, TouchableOpacity } from "react-native";
import { GlobalStyles } from "../components/globals/GlobalStyles";
import * as Animatable from "react-native-animatable";
import Footer from "../components/globals/Footer";
import { LocalStyles } from "../components/locals/LocalStyles";
import { AntDesign, Feather } from "@expo/vector-icons";
import newData from "../data/organisations.json";
import Accordion from "react-native-collapsible/Accordion";
import { useNavigation } from "@react-navigation/native";

export default function AccordionView() {
  const navigation = useNavigation();
  const needs = [];
  newData.map((data) => needs.push(data.Needs.split(", ")));
  const needsFlat = needs.flat(1);
  const finalNeeds = needsFlat.filter(
    (item, index) => needsFlat.indexOf(item) === index
  );

  const SECTIONS = [
    { title: "Areas of Support", data: finalNeeds, fieldToSearch: "Needs" },
    {
      title: "Gender",
      data: ["Women", "Men", "Other"],
      fieldToSearch: "Gender",
    },
    {
      title: "Age Group",
      data: ["Children", "Youth", "Adults"],
      fieldToSearch: "Age",
    },
    {
      title: "Location",
      data: [...new Set(newData.map((org) => org.Code_region))],
      fieldToSearch: "Code_region",
    },
  ];

  const [state, setState] = useState({
    activeSections: [],
    all: [],
  });

  const [checked, setChecked] = useState([]);

  const handlePress = (item, fieldToSearch) => {
    setChecked(
      checked.some((a) => a?.data?.includes(item))
        ? checked.filter((a) => !a?.data?.includes(item))
        : [...checked, { data: item, fieldToSearch: fieldToSearch }]
    );
  };

  const renderHeader = (section, _, isActive) => (
    <Animatable.View duration={400}>
      <View style={LocalStyles.topFilterSection}>
        <Text style={LocalStyles.accordionTextTitle}>{section.title}</Text>
        {isActive ? (
          <AntDesign name="minus" size={24} color="#212121" />
        ) : (
          <AntDesign name="plus" size={24} color="#212121" />
        )}
      </View>
    </Animatable.View>
  );

  const _renderContent = (section) => {
    return section?.data.map((item, index) => {
      if (index >= 0) {
        return (
          <TouchableOpacity
            onPress={() => handlePress(item, section.fieldToSearch)}
            key={item}
          >
            <View style={LocalStyles.subCategories}>
              <View style={LocalStyles.checkboxItems}>
                {checked.some((a) => a?.data?.includes(item)) ? (
                  <AntDesign name="checksquare" size={24} color="#212121" />
                ) : (
                  <Feather name="square" size={24} color="#212121" />
                )}
                <Text style={LocalStyles.checkboxText}>{item} </Text>
              </View>
            </View>
          </TouchableOpacity>
        );
      }
    });
  };

  const _updateSections = (activeSections) => {
    setState({ activeSections });
  };
  let fieldToSearch = [];

  checked?.map((filter) => {
    fieldToSearch = [...fieldToSearch, filter.fieldToSearch];
  });

  return (
    <ScrollView style={{ backgroundColor: "white" }}>
      <ScrollView style={GlobalStyles.filtersContainer}>
        <Accordion
          sections={SECTIONS}
          activeSections={state.activeSections}
          renderHeader={renderHeader}
          renderContent={_renderContent}
          onChange={_updateSections}
        />
      </ScrollView>
      <ScrollView>
        <View style={GlobalStyles.flexCenter}>
          <TouchableOpacity
            style={LocalStyles.resultsButton}
            onPress={() => {
              navigation.navigate("Results", {
                selectedFilters: {
                  fieldToSearch: fieldToSearch,
                  selectedFilter: checked?.map((filter) => filter.data),
                },
              });
            }}
          >
            <Text style={LocalStyles.resultsButtonText}>See results</Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
      <Footer />
    </ScrollView>
  );
}
